#nav {
    height: 100px;
    display: flex;
}

#lognom {
    display: flex;
    justify-content: center;
    align-items: center;
}

#logo1 {
    height: 100%;
    width: 100wh;
}

#nom {
    font-size: 30px;
    margin-left: 2%;
    font-family: "Inter", sans-serif;
}

#links {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 20%;
    padding-right: 15%;  
}

#links a {
    font-size: 20px;
    font-family: "Inter", sans-serif;
    font-weight: bold;
    padding-left: 40%;
    color: black;
    text-decoration: none;
}

#links a:hover {
    text-decoration: underline;
    color: rgb(32, 32, 32);
    transition-duration: 250ms;
}

#links a:active {
    color: rgb(155, 158, 161);
    transition-duration: 0;
}

#sepa {
    height: 2px;
    background-color: rgb(36, 12, 8);
    margin-bottom: 20px;
}